.header {
  &__body {
    padding: 12px 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &__logo {
    width: 152px;
    height: 52px;
  }
  &__search {
    width: 677px;
    height: 48px;

    background-color: #f7f7f7;
    border-radius: 4px;
    padding: 12px 16px;
    display: flex;
    align-items: center;
    .img_icon {
      margin-left: auto;
    }

    input {
      border: none;
      background-color: transparent;
      margin-left: 12px;
      width: 50%;
    }
  }
  &__card_wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 348px;
    height: 44px;
  }

  &__card {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    &:hover {
      cursor: pointer;
    }

    p {
      font-family: "Poppins", sans-serif;
      font-style: normal;
      font-weight: 400;
      font-size: 15px;
      line-height: 20px;

      color: #5c596d;
    }
  }
}

// nav

.nav {
  background-color: #f8f8f8;
  &__list {
    display: flex;
  }

  &__item {
    padding: 10px 16px;
    &:hover {
      box-shadow: 0px 0px 2px rgba(0, 0, 0, 0.274);
      cursor: pointer;
      transition: 300ms;
    }

   
  }

  &__link {
    font-family: "Poppins", sans-serif;
    font-weight: 400;
    font-size: 15px;
    line-height: 20px;
   
    color: #5c596d;
  }
}
