.homeTech {
  padding: 32px 0 64px;

  h2 {
    font-family: "Poppins", sans-serif;
    font-weight: 700;
    font-size: 24px;
    line-height: 28px;
    margin-bottom: 24px;
    color: #5c596d;
  }

  &__wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}

.homeTechCard {
  width: 235px;
  height: 295px;
  left: 0px;
  top: 0px;
  padding: 24px 32px;
  background-color: #f7f7f7;
  border-radius: 8px;

  display: flex;
  flex-direction: column;
  align-items: center;

  h4 {
    font-family: "Poppins", sans-serif;
    font-weight: 700;
    font-size: 20px;
    line-height: 24px;
    color: #5c596d;
    margin-top: 24px;
  }
}
