.contact {
  &__wrapper {
   
    display: flex;
    justify-content: space-between;
  }
  &__body {
    margin: 32px 0;
    padding: 64px 0;
  }

  &__info {
    h4 {
      font-family: "Poppins", sans-serif;
      font-weight: 500;
      font-size: 20px;
      line-height: 24px;
      color: #1d1a2f;
      margin-bottom: 16px;
    }
    li {
        margin-bottom: 12px;

    }

    a {
      font-family: "Poppins", sans-serif;
      font-weight: 400;
      font-size: 16px;
      line-height: 20px;
      color: #5c596d;
    }

    .pay_option {
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 216px;
        
    }

    .social {
        display: flex;
        img {
            margin-right: 24px;
        }
    }
  }

  &__bottom {
    display: flex;
    width: 619px;
    align-items: center;
    justify-content: space-between;
    margin-top: 64px;

    h4 {
      font-family: "Poppins", sans-serif;
      font-weight: 500;
      font-size: 20px;
      line-height: 24px;
      color: #1d1a2f;
      margin-bottom: 16px;
    }

    a {
      font-family: "Poppins", sans-serif;
      font-weight: 400;
      font-size: 16px;
      line-height: 20px;
      color: #5c596d;
    }
  }
}
