.brand {
  padding: 32px 0 64px;

  &__menu {
    h2 {
      font-family: "Poppins", sans-serif;
      font-weight: 700;
      font-size: 24px;
      line-height: 28px;
      margin-bottom: 24px;
      color: #5c596d;
    }
  }

  &__list {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 24px;
  }

  &__item {
    width: 156px;
    height: 36px;
    background-color: #f7f7f7;
    border-radius: 4px;
    display: flex;
    align-items: center;
    justify-content: center;
    &:hover {
      border: 1px solid #b3c8fd;
      cursor: pointer;
      transition: 300ms;
    }

    &:hover a {
      color: #1b5bf7;
      transition: 300ms;
    }

    a {
      font-family: "Poppins", sans-serif;
      font-weight: 400;
      font-size: 15px;
      line-height: 20px;

      color: #5c596d;
    }
  }

  &__logo {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;

    &__item {
      width: 206px;
      height: 75px;
      margin-bottom: 12px;
      border: 1px solid #eae9ef;
      border-radius: 4px;

      display: flex;
      align-items: center;
      justify-content: center;

      &:hover {
          cursor: pointer;
          border: 1px solid #b3c8fd;
          transition: 300ms;
    
      }
    }
  }
}
